import { getDatabase, ref, child, push, update, get, remove } from "firebase/database";

export const databaseFunction = ( app ) => { 
    return {
		refDatabase: ref(getDatabase(app)),
		refSingleNode: function (no) {
			return ref(getDatabase(app), no);
		},
		getOnDatabase(node) {
			return new Promise((resolve) => {
				get(child(this.refDatabase, node)).then((snapshot) => resolve(snapshot.val()));
			});
		},
		createOnDatabase: function (no, form) {
			push(child(this.refDatabase, no), form);
		},
		updateOnDatabase: function (no, id, form) {
			const updates = {};

			updates[`/${id}`] = form;
			return update(this.refSingleNode(no), updates);
		},
		deleteOnDatabase: function (no, id) {
			return new Promise((resolve) => { 
                remove(this.refSingleNode(`${no}/${id}`)) 
                resolve()
            });
		},
	};
};
