<template>
  <div class="blog">
    <div class="header d-flex justify-content-between">
        <p>Posts do blog</p>
        <b-button class="btn-base primary-btn btn-novo-post" @click="showModal = true">Novo Post</b-button>
    </div>
    <b-table striped hover responsive :items="items" :fields="headerFields">
      <template v-slot:cell(id)="{ item }">
        <div class="div-table-btns">
          <b-button class="btn-base btn-table edit" @click="openModal(item)">
            <b-icon-pencil class="listing-user-table-item-icon"/>
          </b-button>
          <b-button class="btn-base btn-table delete" @click="destroy(item.id)">
            <b-icon-trash class="listing-user-table-item-icon" />
          </b-button>
        </div>
      </template>

    </b-table>
    <ModalAdicionarPost :post="editModal" @closed="closeModal" v-if="showModal == true"/>
  </div>
</template>

<script>
import ModalAdicionarPost from "./ModalAdicionarPost.vue"

let headerFields = [
    {key: 'titulo', label: "Titulo"},
    {key: 'date', label: 'Data'},
    {key: 'id', label: 'Ações'},
]

  export default {
    components: {
      ModalAdicionarPost
    },
    data() {
      return {
        headerFields: [ ...headerFields ],
        items: [],
        showModal: null,
        editModal: null
      }
    },
    methods: {
      openModal( edit ){
        this.editModal = edit
        this.showModal = true
      },
      closeModal(){
        this.showModal = null
        this.editModal = null
        this.loadPosts()
      },
      async loadPosts(){
        const myObject = await this.$database.getOnDatabase("/post")
        const myArray = Object.keys(myObject).map(key => ({ id: key, ...myObject[key] }));
        this.items = [...myArray]
      },
      async destroy( id ){
        await this.$database.deleteOnDatabase("/post", id)
        this.loadPosts()
      }
    },
    async created(){
        this.loadPosts()
    }
  }
</script>

<style scoped>
.blog{
    padding: 30px 10%;
}

.blog .header p{
    font-size: 26px;
    font-weight: 600;
    letter-spacing: 2px;
    color: #000;
    text-transform: uppercase;
}

.btn-novo-post{
  padding: 5px 10px !important;
  max-height: 35px !important;
  border-radius: 3px;
  font-size: 14px;
  background-color: #0f3b23 !important;
  color: #fff !important;
}

.btn-table{
  width: 40px !important;
  height: 40px !important;
  padding: 5px 10px 10px 10px;
  border: initial;
  font-size: 18px;
  margin-right: 10px;
  border-radius: 10px;
}

.btn-table.edit{
  background-color: #0f3b23 !important;
}

.btn-table.edit:hover{
  background-color: #2bfc93 !important;
}

.btn-table.delete{
  background-color: #f50000 !important;
}

.btn-table.delete:hover{
  background-color: #9e0404 !important;
}

@media (max-width: 930px) {
    .blog{
      padding: 30px 5%;
    }

    .btn-novo-post{
      padding: 2px 5px !important;
      max-height: 40px !important;
      min-width: fit-content !important;
      font-size: 14px;
    }

    .btn-table{
      width: 40px !important;
      height: 40px !important;
      border: initial;
      font-size: 16px;
    }

    .div-table-btns{
      min-width: 90px;
    }
}
</style>