import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

export const storageFunction = (app) => {
	return {
		refStorage: ref(getStorage(app)),
		refSingleStorage: function (no) { return ref(getStorage(app), no)},
		uploadOnStorage: function (no, file) {
			return new Promise((resolve) => {
                uploadBytes(this.refSingleStorage(no), file)
                .then(async (snapshot) => { resolve(await getDownloadURL(snapshot.ref)) });
            })
		},
		getSingleURL: function ( no ) {
			return new Promise((resolve) => {
				resolve( getDownloadURL( this.refSingleStorage(no) ) ); 
			})
		}
	};
};
