<template>
    <div class="painel">
        <Login v-if="!isLogado" @logar="logar" @deslogar="islogado = false"/>
        <Dashboard v-else @deslogar="islogado = false"/>
        <!-- <Dashboard @deslogar="islogado = false"/> -->
    </div>
</template>

<script>
import Login from "./components/Login.vue"
import Dashboard from "./components/Dashboard/Dashboard.vue"

export default {
    components:{
        Login,
        Dashboard
    },
    data(){
        return {
            isLogado: false
        }
    },
    methods: {
        logar(){
            this.isLogado = true
        }
     }
}
</script>

