<template>
    <b-modal id="modal-adicionar-post" centered  modal-class="modal" size="md" hide-header-close @hidden="$emit('closed')" :visible="true">
        <template #modal-title >
                ADICIONAR POST
        </template>

        <b-form-group label="Título:" label-for="titulo" class="mb-3">
                <b-form-input id="titulo" v-model.lazy="form.titulo" maxlength="30" />
                <b-form-invalid-feedback :state="validate.titulo ? false : null">{{ validate.titulo }}</b-form-invalid-feedback>
        </b-form-group>
        
        <b-form-group label="Anexar Imagem" label-for="file" class="mb-3">
            <b-form-file name="file" v-model="file" accept=".jpeg, .png, .jpg"/>
            <b-form-invalid-feedback :state="validate.file ? false : null">{{ validate.file }}</b-form-invalid-feedback>
        </b-form-group>

        <b-form-group label="Conteúdo do Post: " label-for="conteudo" class="mb-3">
            <b-form-textarea id="conteudo" no-resize placeholder="Digite aqui" maxlength="500" rows="4" max-rows="4" v-model="form.conteudo" />
            <b-form-invalid-feedback :state="validate.conteudo ? false : null">{{ validate.conteudo }}</b-form-invalid-feedback>
        </b-form-group>

        <template #modal-footer>
          <b-button class="btn-base btn-modal-novo-post cancel" @click="$bvModal.hide('modal-adicionar-post')">
              CANCELAR
          </b-button>
          <b-button class="btn-base btn-modal-novo-post" @click="upload">
              SALVAR
          </b-button>
        </template>
    </b-modal>
</template>

<script>
export default {
    props:{
        post: Object
    },
    data() {
      return {
        form: { titulo: null, conteudo: null, date: null, linkImg: null},
        validate: { titulo: null, conteudo: null, date: null},
        file: null
      }
    },
    methods: {
        async upload() {
            if(!this.isValid()) return

            if(this.file != null) {
                this.form.linkImg = await this.$storage.uploadOnStorage(`imagens/post/${this.file.name}`, this.file)
            }

            if(this.date ==  null) {
                this.form.date = this.obterDataFormatada()
            }

            let dataForm = {
                    titulo: this.form.titulo,
                    date: this.form.date,
                    conteudo: this.form.conteudo,
                    linkImg: this.form.linkImg,
                }
            
            if (this.post == null) {
                this.$database.createOnDatabase("post", dataForm);
            }
            else {
                this.$database.updateOnDatabase("post", this.post.id, dataForm)
            }
            
            this.$emit('closed')
        },
        isValid(){
            this.validate = { titulo: null, conteudo: null, file: null}

            if(this.form.titulo == null){
                this.validate.titulo = "*Campo obrigatório."
            }

            if(this.form.conteudo == null){
                this.validate.conteudo = "*Campo obrigatório."
            }

            if(!this.post && this.file == null){
                this.validate.file = "*Campo obrigatório."
            }

            return this.validate.titulo || this.validate.conteudo || this.validate.file ? false : true
        },
        async excluir( url ){
            await this.$storage.refFromURL( url ).delete()
        },
        obterDataFormatada() {
            const agora = new Date();
            const formato = (valor) => valor.toString().padStart(2, '0');

            return `${formato(agora.getDate())}/${formato(agora.getMonth() + 1)}/${agora.getFullYear()} - ${formato(agora.getHours())}:${formato(agora.getMinutes())}:${formato(agora.getSeconds())}`;
        }
    },
    created(){
        if (this.post != null){
            this.form = { 
                titulo: this.post.titulo, 
                conteudo: this.post.conteudo, 
                date: this.post.date,
                linkImg: this.post.linkImg
            }
        }
    }
  } 
</script>

<style scoped>
.btn-modal-novo-post{
    padding: 5px 10px !important;
    max-height: 35px !important;
    border-radius: 3px;
    font-size: 14px;
    background-color: #0f3b23 !important;
    color: #fff !important;
}

.btn-modal-novo-post.cancel{
    background-color: #949292 !important;
    color: #fff !important;
}
</style>

<style>
.modal-content{
    background-color: #eefdf5 !important;
}

.modal{
    background-color: rgba(0, 0, 0, 0.267) !important;
}
</style>