    <video autoplay name="media" class="video">
      <!-- <source src="https://firebasestorage.googleapis.com/v0/b/dote-290b0.appspot.com/o/into-dote.mp4?alt=media&amp;token=f939cef1-716d-47bd-a0d4-48f5b9dd1345" type="video/mp4"> -->
      <source src="./into-dote.webm" type="video/webm">
    </video>
  </div>
</template>

<script>
export default {
  data() {
    return {
      videoSource: null,
      isPlaying: true,
    };
  },
  methods: {},
  created(){
    setTimeout(() => {
      this.isPlaying = false
    }, 5000)
  }
};
</script>

<style scoped>
.openning{
  max-height: 100vh;
  max-width: 100vw;
