  z-index: 10000;
  background-color: #000;
  display: none;
}

.openning .video{
  max-height: 100vh;
  min-height: 100vh;
  max-width: 100vw;
  min-width: 100vw;
}

/* Estilos adicionais conforme necessário */
  video {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto; /* Centraliza o vídeo na tela */
  }

  @media (max-width: 930px) {
    .openning{
      display: block
    }
