<template>
    <div class="dashboard">
        <Navbar @change="change" @deslogar="$emit('deslogar')"/>
        <Blog v-if="view == 'blog'" />
    </div>
</template>

<script>
import Navbar from "./Navbar.vue"
import Blog from "./Blog.vue"

export default {
    components:{ 
        Navbar,
        Blog
    },
    data(){
        return { 
            view: null
        }
    },
    methods: {
        change( view ){
            this.view = view
        }
    }
}
</script>

<style scoped >
.dashboard{
    background-color: #eefdf5 !important;
    min-height: 100vh;
    min-width: 100vw;
}
</style>